import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'vant/lib/index.css' // 引入 Vant 样式
import { Button, Cell, Tabbar, TabbarItem, NavBar, Icon, Swipe, SwipeItem, Toast, Lazyload, Sticky, Field, Loading, Empty, Tag, Grid, GridItem } from 'vant' // 注册 Cell 组件
Vue.use(Button) // 注册 Button 组件
Vue.use(Cell)
Vue.use(Tabbar)
Vue.use(TabbarItem)
Vue.use(NavBar)
Vue.use(Icon)
Vue.use(Swipe)
Vue.use(SwipeItem)
Vue.use(Toast)
Vue.use(Lazyload)
Vue.use(Sticky)
Vue.use(Field)
Vue.use(Loading)
Vue.use(Empty)
Vue.use(Tag)
Vue.use(Grid)
Vue.use(GridItem)

Vue.config.productionTip = false

