router.beforeEach((to, from, next) => {
  const isHideTabbar = to.meta && to.meta.hideTabbar
  // 如果需要隐藏底部导航栏，就向组件发送一个 hideTabbar 事件
  if (isHideTabbar) {
    store.commit('setHideTabbar', true)
  } else {
    store.commit('setHideTabbar', false)
  }
  next()
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
