import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/homes/HomeView.vue'
import CateryView from '../views/CateryView.vue'
import ShopCartView from '../views/ShopCartView.vue'
import MineView from '../views/mines/MineView.vue'
import LoginView from '../views/logins/LoginView.vue'
import Regisview from '../views/logins/RegisTelview.vue'
import ErrorPage from '../views/logins/ErrorPage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: HomeView
  },

  {
    path: '/errorPage',
    component: ErrorPage,
    meta: { hideTabbar: true }
  },

  {
    path: '/home',
    component: HomeView
  },
  {
    path: '/catery',
    name: 'catery',
    component: CateryView
  },
  {
    path: '/cart',
    name: 'cart',
    component: ShopCartView
  },
  {
    path: '/mine',
    name: 'mine',
    component: MineView
  },
  {
    path: '/login',
    name: 'login',
    component: LoginView,
    meta: { hideTabbar: true }
  },
  {
    path: '/regis',
    name: 'regis',
    component: Regisview,
    meta: { hideTabbar: true }
  },
  {
    path: '*', // 匹配任何路径
    redirect: '/errorPage' // 404 页面组件

  }

]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
