      </van-cell-group>
    </div>

    <van-button
      style=" border-radius: 10px; width: 190px; margin: 0 auto; margin-top: 20px;"
      @click="onLoginClick" type="primary" block>立即登录</van-button>

      <div style="display: flex; flex-direction: row; justify-content: space-between; padding: 0px 20px;">

        <router-link to="/regis"><button class="regis">注册账号</button></router-link>
        <router-link to="/regis"><button class="regis">忘记密码</button></router-link>

