<script>
export default {
  data () {
    return {
      tel: '18736972656',
      pwd: '123456'
    }
  },

  methods: {
    onBackClick () {
      console.log(this.$router)
      this.$router.back()
    },
    onLoginClick () {
      if (this.tel === '18736972656' && this.pwd === '123456') {
        console.log('为什么不对呢')
        this.$toast.loading({
          message: '',
          forbidClick: true
        })

        setTimeout(() => {
          this.$toast.clear()
          this.$toast.success('登录成功')
          this.$router.push('/home')
          this.$store.commit('setTabbarActive', 0)
        }, 1000)
      } else {
        console.log('为什么不对呢22')
        this.$toast.fail('账号密码有误')
      }
    }
  }
}
</script>

