<template>
  <div id="app">
    <router-view/>
    <van-tabbar v-model="tabbarActive" v-if="!hideTabbar">
      <van-tabbar-item @click="_setActive(0)" replace to="/home" icon="home-o">首页</van-tabbar-item>
      <van-tabbar-item @click="_setActive(1)" replace to="/catery" icon="apps-o">分类</van-tabbar-item>
      <van-tabbar-item @click="_setActive(2)" replace to="/cart" icon="cart-o">购物车</van-tabbar-item>
      <van-tabbar-item @click="_setActive(3)" replace to="/mine" icon="contact-o">个人中心</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  components: {

  },
  computed: {
    ...mapState(['hideTabbar']), // 将 hideTabbar 状态映射到组件的计算属性中
    ...mapState(['tabbarActive'])
  },
  data () {
    return {
      active: 0

    }
  },
  methods: {
    _onclick () {
      this.active = 1
      console.log(66666)
    },

    _setActive (index) {
      this.$store.commit('setTabbarActive', index) // 调用 Vuex 的 mutation 更新 active 状态
    }
  },
  created () {
    this.$router.beforeEach((to, from, next) => {
      this.hideTabbar = to.meta.hideTabbar || false
      next()
    })
  }
}
</script>

